.app-container {
  max-width: 500px;
  margin: 0 auto;
  padding: 10px 10px 30px;
  border: 2px solid gray;
  background: aliceblue;
}
.current-team-summary {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  font-size: 24px;
  font-family: comic Sans, Comic Sans MS, cursive;
}

.previous-team-summary {
  font-size: 14px;
  color: darkslategray;
}

.runs-container, .extra-runs-container, .out-container, .next-ball-container {
  display: flex;
  justify-content: flex-start;
  margin-top: 15px;
}

.next-ball-container {
    justify-content: center;
}

.extra-label {
    margin-right: 10px;
}

.score-button {
    margin-right: 10px;
    border: 1px solid;
    border-radius: 5px;
    cursor: pointer;
}

button.disabled {
    pointer-events: none;
    cursor: not-allowed;
}
.current-over-details {
    text-align: left;
    /* padding: 1em 1em; */
}
